<template>
  <div id="news" @touchstart="hideList">
    <Carousel :main-item="mainItem"></Carousel>
    <div class="main-content">
      <div class="page" :style="renderPage(0)">
        <div class="page-content">
          <div class="main">
            <div class="headline left">
              <p class="pointer">中国移动：北上广已进行5G外场测试 2020年正式商用</p>
              <p>中国移动5G计划一直备受关注。从今年起，中国移动进行外场测试，预计到2020年将正式进入商用阶段。目前，其已经在北京、上海、广州进行了5G外场测试。</p>
            </div>
            <div class="key-news">
              <div v-for="(item, index) in keyNewsList" v-bind:key="index">
                <p class="pointer">{{item.title}}</p>
                <p>{{item.date}}</p>
              </div>
            </div>
          </div>
          <div class="views">
            <div class="views-name">
              <p>观点</p>
            </div>
            <div class="viewsList">
              <div v-for="(item, index) in viewsList" v-bind:key="index">
                <p>{{item.content}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="page" :style="renderPage(1)">
        <div class="page-content">
          <div v-for="(item, index) in story"  v-bind:key="index" class="box left" :style="{'background': 'url(' + item.bg + ')'}">
            <div class="highlight"></div>
            <p>{{item.title}}</p>
          </div>
        </div>
      </div>
      <div class="page" :style="renderPage(2)">
        <div class="page-content">
          <div class="newsBar">
            <p class="left">全部新闻</p>
            <div class="search pointer left"></div>
          </div>
          <div class="newsList">
            <div v-for="(item, index) in allNews" v-bind:key="index">
              <div class="bg" :style="{'background': 'url(' + item.bg + ')'}"></div>
              <p class="pointer">{{item.title}}</p>
              <p>{{item.date}}</p>
              <p class="pointer">{{item.summary}}</p>
            </div>
          </div>
          <div class="seeMore pointer">
            <p>阅读更多</p>
          </div>
        </div>
      </div>
      <foot></foot>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Carousel from './Carousel'
import foot from './Foot'
export default {
  name: 'news',
  data () {
    return {
      mainItem: [],
      keyNewsList: [
        {title: '中国平安战略携手华为助力中国智慧城市建设', date: '03月10日'},
        {title: 'Uber自动驾驶卡车开始上路运货，未来人类货车司机该何去何从？', date: '03月10日'},
        {title: '人工智能：让我们加速向未来 人机搭配干活不累', date: '03月10日'},
        {title: '华为成欧洲专利局最大专利申请人', date: '03月09日'},
        {title: '预测：2018年中国电信的收入将增长到约550亿美元', date: '03月09日'},
        {title: '华为携手沃达丰完成全球首个基于3GPP标准5G商用系统的通话与双连接测试', date: '02月26日'},
        {title: 'AI竞赛持续升温 巨头纷纷入局分羹', date: '02月28日'}
      ],
      viewsList: [
        {content: '童文：5G是一个生态系统 合作最重要'},
        {content: '苗圩：中国已经着手研究6G'},
        {content: 'Aicha：5G是人类和技术可能性的巨大飞跃'},
        {content: '尚冰：2018年中国移动4G用户将破7亿'},
        {content: 'Rajeev：世界各大运营商加速 5G部署将提前一年'},
        {content: '余承东：未来手机厂商或不到四家 华为迟早是老大'},
        {content: '张云勇：加快完善工业互联网产业生态体系'}
      ],
      story: [
        {title: '自动驾驶汽车如何改变一座城？', bg: 'resource/news/modal_s.jpg'},
        {title: '自动驾驶汽车如何改变一座城？', bg: 'resource/news/modal_s.jpg'},
        {title: '自动驾驶汽车如何改变一座城？', bg: 'resource/news/modal_s.jpg'},
        {title: '自动驾驶汽车如何改变一座城？', bg: 'resource/news/modal_s.jpg'},
        {title: '自动驾驶汽车如何改变一座城？', bg: 'resource/news/modal_s.jpg'},
        {title: '自动驾驶汽车如何改变一座城？', bg: 'resource/news/modal_s.jpg'}
      ],
      allNews: [
        {title: '中国平安战略携手华为助力中国智慧城市建设', date: '03月10日', bg: 'resource/news/modal_s.jpg', summary: '日前，中国平安保险（集团）股份有限公司与华为技术有限公司签署战略合作协议，双方将在智慧城市、云业务等领域开展全方位的合作，本次战略合作的达成，将进一步推动双方之间的资源共享和优势互补，助力中国智慧城市发展及金融科技创新。'},
        {title: 'Uber自动驾驶卡车开始上路运货，未来人类货车司机该何去何从？', date: '03月10日', bg: 'resource/news/modal_s.jpg', summary: '全程344英里，一辆Uber自动驾驶卡车从中西部地区穿越亚利桑那州，一路开到南加州。这还不算什么，精彩之处在于，当Larry和Mark两个男人在“产铜之州”（亚利桑那州）最西边碰头时，经过安全驾驶培训的司机Larry刚完成了自动驾驶之旅，Mark则驾着一辆传统卡车从洛杉矶来到这里。'},
        {title: '人工智能：让我们加速向未来 人机搭配干活不累', date: '03月10日', bg: 'resource/news/modal_s.jpg', summary: '目前，人工智能已经成为国家发展战略，去年7月，国务院下发的《新一代人工智能发展规划》中明确阐述，到2030年，中国人工智能理论、技术与应用总体超越平均水平，成为世界主要人工智能创新中心。'},
        {title: '华为成欧洲专利局最大专利申请人', date: '03月09日', bg: 'resource/news/modal_s.jpg', summary: '华为高级副总裁兼首席法务官宋柳平博士表示：“华为非常重视研发工作，因此创建高专利数是一个自然而然的结果。 “随着我们越来越多地参与设置下一代电信标准，如5G，这些专利投入数字可能会进一步上升。”'},
        {title: '预测：2018年中国电信的收入将增长到约550亿美元', date: '03月09日', bg: 'resource/news/modal_s.jpg', summary: '展望近期，中国电信的用户群将在本季度增长，主要由4G增加推动。除了无线用户增长之外，该运营商的利润率还应从设备补贴成本的降低中受益。与此同时，由于固定电话用户基础和固定电话ARPU的压力，固定电话业务可能继续下滑。'},
        {title: '华为携手沃达丰完成全球首个基于3GPP标准5G商用系统的通话与双连接测试', date: '02月26日', bg: 'resource/news/modal_s.jpg', summary: '本次测试包括4G与5G网络双连接的技术，并实现通话测试。互联网语音协议（VOIP）的控制消息在4G网络承载，业务数据在5G网络承载。此外也在该网络中测试了高清视频通话业务。该测试使用5G端到端商用系统，采用了3.7GHz频谱，并通过华为无线接入网络和核心网络设备，实现基于微服务的架构、控制面/用户面分离、接入无关性和网络切片，以支撑业务测试。'},
        {title: 'AI竞赛持续升温 巨头纷纷入局分羹', date: '02月28日', bg: 'resource/news/modal_s.jpg', summary: '据外媒统计数据显示，今年通讯和手机类参会厂商依旧超过了50%的比例，不过，人工智能类的技术厂商占比已经达到了10%，紧随其后的是IoT（物联网）技术。此外，中国AI新创企业募集资的金额首度超越美国跃居全球之冠，可见中国在AI发展方面势头也越来越猛。'}
      ]
    }
  },
  methods: {
    hideList: function () {
      this.$root.eventHub.$emit('hide')
    },
    renderPage: function (i) {
      if (i % 2 === 0) {
        return 'background-color: white;'
      } else {
        return 'background-color: #F0F2F5;'
      }
    }
  },
  components: {
    Carousel,
    foot
  },
  created () {
    var self = this
    $.ajax({
      async: true,
      url: '/carousel/',
      type: 'GET',
      data: {},
      success: function (data) {
        for (var i = 0; i < data.length; i++) {
          if (data[i].title.indexOf('≮') === -1) {
            self.mainItem.push({
              id: data[i].news_id,
              caption: '',
              caption2: data[i].title.split('≮')[0],
              bg: data[i].bg
            })
          } else {
            self.mainItem.push({
              id: data[i].news_id,
              caption: data[i].title.split('≮')[0],
              caption2: data[i].title.split('≮')[1],
              bg: data[i].bg
            })
          }
        }
      }
    })
  }
}
</script>

<style scoped>
  .main-item p{
    color: white;
  }
  .main-item p:last-of-type{
    display: inline-block;
  }
  .main-item:first-of-type p:last-of-type{
    font-family: Xingkai!important;
  }
  .main-content{
    width: 100%;
    height: auto;
  }
  .page{
    width: 100%;
    height: auto;
    position: relative;
    overflow: hidden;
  }
  .page-content{
    width: calc(100% - 40px);
    height: auto;
    overflow: hidden;
  }
  .headline{
    width: 100%;
    height: auto;
    margin: 0 0 20px;
  }
  .headline p:first-of-type{
    font-weight: bold;
    padding-bottom: 6px;
  }
  .key-news{
    width: 100%;
    height: auto;
    float: left;
  }
  .key-news>div{
    width: 100%;
    height: auto;
    padding: 18px 0;
    overflow: hidden;
  }
  .views{
    width: 100%;
    height: auto;
    float: left;
    margin-top: 16px;
  }
  .views-name{
    width: 100%;
    padding: 18px 0;
    border-bottom: 1px solid #E6E6E6;
  }
  .viewsList{
    width: 100%;
    height: auto;
  }
  .viewsList>div{
    width: 100%;
    height: auto;
  }
  .viewsList>div:not(:last-of-type){
    border-bottom: 1px solid #E6E6E6;
  }
  .viewsList>div p{
    font-size: 18px;
    line-height: 150%;
    cursor: pointer;
    margin: 18px 0;
  }
  .box{
    background-position: 40% 50%!important;
    background-size: cover!important;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    -webkit-transition: background-position .3s;
    -moz-transition: background-position .3s;
    -ms-transition: background-position .3s;
    -o-transition: background-position .3s;
    transition: background-position .3s;
  }
  .box:hover{
    background-position: 60% 50%!important;
  }
  .highlight{
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0, 0, 0, 0);
    -webkit-transition: background-color .3s;
    -moz-transition: background-color .3s;
    -ms-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
  }
  .box:hover .highlight{
    background: rgba(0, 0, 0, 0.5);
  }
  .box p{
    width: calc(100% - 40px);
    margin: 0 10px;
    font-size: 22px;
    line-height: 140%;
    color: white;
    position: relative;
    top: 100%;
    -webkit-transition: transform .3s;
    -moz-transition: transform .3s;
    -ms-transition: transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
  }
  .box:hover p{
    -webkit-transform: translateY(calc(-100% - 40px));
    -moz-transform: translateY(calc(-100% - 40px));
    -ms-transform: translateY(calc(-100% - 40px));
    -o-transform: translateY(calc(-100% - 40px));
    transform: translateY(calc(-100% - 40px));
  }
  .newsBar{
    overflow: hidden;
    border-bottom: 1px solid #E6E6E6;
  }
  .newsList>div{
    width: 100%;
    padding: 20px 0;
    overflow: hidden;
  }
  .newsList>div p:nth-of-type(2){
    font-size: 16px;
    line-height: 18px;
  }
  .newsList>div:not(:last-of-type){
    border-bottom: 1px solid #E6E6E6;
  }
  .bg{
    background-position: 50% 50%!important;
    background-size: cover!important;
    float: left;
  }

  @media only screen and (max-device-width: 767px) {
    .page-content{
      margin: 20px;
    }
    .page:nth-of-type(3) .page-content{
      margin: 0 20px 20px 20px;
    }
    .headline p:first-of-type{
      font-size: 24px;
      line-height: 130%;
    }
    .headline p:last-of-type{
      font-size: 16px;
      line-height: 150%;
    }
    .key-news>div:first-of-type{
      border-top: 1px solid #E6E6E6;
    }
    .key-news>div:not(:last-of-type){
      border-bottom: 1px solid #E6E6E6;
    }
    .key-news>div p:first-of-type{
      font-size: 18px;
      line-height: 24px;
      font-weight: bold;
    }
    .key-news>div p:last-of-type{
      font-size: 14px;
      line-height: 20px;
      padding-top: 6px;
      user-select: none;
    }
    .views-name p{
      font-size: 18px;
      font-weight: bold;
    }
    .box{
      width: 100%;
      height: 240px;
      margin: 10px 0;
    }
    .newsBar p:first-of-type{
      font-size: 18px;
      padding: 20px 0 12px 0;
    }
    .search{
      width: 40px;
      height: 40px;
      background: url("../../resource/assets/news_search.png") 50% 50% no-repeat;
      background-size: 50%;
      margin: 8px 0 0 12px;
    }
    .bg{
      width: 150px;
      height: 100px;
      margin-right: 10px;
    }
    .newsList>div p{
      line-height: 140%;
    }
    .newsList>div p:first-of-type{
      width: calc(100% - 160px);
      font-size: 18px;
      float: left;
      padding: 0 0 6px;
      max-height: 85px;
      overflow: hidden;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
    }
    .newsList>div p:last-of-type{
      display: none;
    }
    .seeMore{
      width: 72px;
      height: 20px;
      margin: 10px calc((100% - 72px) / 2);
    }
    .seeMore p{
      font-size: 18px;
    }
    .seeMore:active{
      color: #00BFFF;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .page-content{
      margin: 40px 20px;
    }
    .headline p:first-of-type{
      font-size: 36px;
      line-height: 150%;
    }
    .headline p:last-of-type{
      font-size: 20px;
      line-height: 140%;
    }
    .key-news>div:first-of-type{
      border-top: 1px solid #E6E6E6;
    }
    .key-news>div:not(:last-of-type){
      border-bottom: 1px solid #E6E6E6;
    }
    .key-news>div p:first-of-type{
      max-width: calc(100% - 150px);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
      float: left;
    }
    .key-news>div p:last-of-type{
      font-size: 18px;
      line-height: 28px;
      user-select: none;
      float: right;
    }
    .views-name p{
      font-size: 24px;
      font-weight: bold;
    }
    .box {
      width: 100%;
      height: 320px;
      margin: 0 0 10px;
    }
    .newsBar p:first-of-type{
      font-size: 20px;
      padding: 0 18px 20px 0;
    }
    .search{
      width: 20px;
      height: 20px;
      background: url("../../resource/assets/news_search.png");
      background-size: cover;
    }
    .bg{
      width: 180px;
      height: 120px;
      margin-right: 30px;
    }
    .newsList>div p{
      line-height: 160%;
    }
    .newsList>div p:first-of-type{
      font-size: 18px;
      padding: 0 0 10px;
      width: calc(100% - 360px);
      float: left;
    }
    .newsList>div p:nth-of-type(2){
      padding: 0 0 10px;
      float: right;
    }
    .newsList>div p:last-of-type{
      width: calc(100% - 210px);
      font-size: 16px;
      float: right;
    }
    .seeMore{
      width: 100px;
      height: 40px;
      margin: 10px calc((100% - 100px) / 2);
    }
    .seeMore p{
      font-size: 20px;
    }
    .seeMore:hover{
      color: #00BFFF;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 680px) {
    .page-content{
      width: 640px;
      margin: 40px calc((100% - 640px) / 2);
    }
    .box{
      width: 640px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 878px) {
    .page-content{
      width: 850px;
      margin: 40px calc((100% - 850px) / 2);
    }
    .main{
      float: left;
      width: 68%;
    }
    .views{
      width: 28%;
      margin-top: 0;
      float: right;
    }
    .box{
      margin: 0 10px 10px 0;
    }
    .box:first-of-type, .box:nth-of-type(4), .box:nth-of-type(5){
      width: 480px;
    }
    .box:nth-of-type(2), .box:nth-of-type(3), .box:nth-of-type(6){
      width: 360px;
    }
    .box:nth-of-type(2), .box:nth-of-type(4), .box:nth-of-type(6){
      margin-right: 0;
    }
    .box:nth-of-type(5), .box:nth-of-type(6){
      margin-bottom: 0;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1094px) {
    .page-content{
      width: 1066px;
      margin: 40px calc((100% - 1066px) / 2);
    }
    .box:first-of-type, .box:nth-of-type(4), .box:nth-of-type(5){
      width: 600px;
    }
    .box:nth-of-type(2), .box:nth-of-type(3), .box:nth-of-type(6){
      width: 456px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1308px) {
    .page-content{
      width: 1280px;
      margin: 40px calc((100% - 1280px) / 2);
