    .box:nth-of-type(2), .box:nth-of-type(4){
      margin-right: 10px;
    }
    .box:nth-of-type(3){
      margin-right: 0;
    }
    .box:nth-of-type(4), .box:nth-of-type(5), .box:nth-of-type(6){
      margin-bottom: 0;
    }
    .box:nth-of-type(4), .box:nth-of-type(5){
      width: 360px;
    }
    .box:nth-of-type(6){
      width: 540px;
      margin-right: 0;
    }
  }
</style>



